.checkbox-container {
  display: block;
  position: relative;
  padding-left: 25px; 
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 16px;
  color: rgb(123, 119, 119); ;
}

/* Checkbox stil */
.checkbox-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Yuvarlak işaret */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 12px;
  width: 12px;
  border: 2px solid #000;;
  border-radius: 50%; /* Yuvarlak şekil */
}


.checkbox-container input:checked ~ .checkmark {
  background-color: #080908;
  width:10px;
  height: 10px;
  border-radius: 50%;
}
